	module.exports = {"data":{"post":{"html":"<p>Ten days after her injury, Mama is now able to hobble across the room without crutches. She's able to handle Christopher in the morning, so I've gone back to my normal work schedule. She's back at work, staying in the office. We imagine that next week she'll be able to drive again. </p>\n<p>All in all she's been a very good patient. I've only had to remind her a couple of times to take it easy. She's gotten good treatment from her next-door neighbor (a physical therapist) and her sister-in-law (a <em>Heilpraktikerin</em>, or nonmedical practitioner). She's only seen her doctor once.</p>","fields":{"title":"Gimp Update","date":"29 Jan 2003","url":"https://papascott.de/2003-01-29-gimp-update/"}}},"pathContext":{"slug":"/2003-01-29-gimp-update/","prev":{"fields":{"title":"On This Day: Challenger","date":"28 Jan 2003","slug":"/2003-01-28-on-this-day-challenger/"}},"next":{"fields":{"title":"American Idols","date":"30 Jan 2003","slug":"/2003-01-30-american-idols/"}}}}

